@import url(https://fonts.googleapis.com/css?family=Oswald:300,400);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body{background-color:#1e2127}.newly-added{-webkit-animation:flyin .5s ease-in forwards;animation:flyin .5s ease-in forwards;opacity:0;-webkit-transform:scale(2);transform:scale(2);-webkit-filter:blur(5px);filter:blur(5px)}@-webkit-keyframes flyin{to{-webkit-filter:blur(0);filter:blur(0);-webkit-transform:scale(1);transform:scale(1);opacity:1}}@keyframes flyin{to{-webkit-filter:blur(0);filter:blur(0);-webkit-transform:scale(1);transform:scale(1);opacity:1}}.app{text-align:center}.app-logo{height:40vmin;pointer-events:none}.responsive-flex{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}@media(max-width: 768px){.responsive-flex{-webkit-flex-direction:column;flex-direction:column}}@media(max-width: 768px){.hide-sm{display:none}}.app-body{padding:0px 20px;background-color:#1e2127;min-height:100vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:flex-start;justify-content:flex-start;font-family:"Open sans",Arial,Helvetica,sans-serif;font-size:calc(10px + 2vmin);color:#fff}.app-body .redeem-title{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;width:50vw}.app-body .redeem-title .redeem-text{margin-top:2vh;text-transform:uppercase}.app-body .redeem-title .reset{max-width:16px;max-height:16px;margin-right:10px}.app-body .redeem-title .reset-button{position:fixed;right:2vw;top:2vh;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;cursor:pointer;font-size:calc(4px + 2vmin)}.app-body .redeem-title .coupon{max-width:50vw;max-height:40vh;margin-top:-15px}.app-body .redeem-title .enter-code{outline:none;height:32px;padding:4px;width:100px;border-radius:4px;font-family:"Oswald",Arial,Helvetica,sans-serif;text-align:center;text-transform:uppercase;border:2px solid #1e2127;box-shadow:5px 5px 9px #0e0f12,-5px -5px 9px #2e333c}.app-body .redeem-title .submit-code{outline:none;margin-left:20px;height:40px;padding:4px;width:100px;border-radius:4px;font-family:"Oswald",Arial,Helvetica,sans-serif;text-align:center;text-transform:uppercase;background-color:#1e2127;color:#ffe5d9;border:2px solid #ffe5d9;box-shadow:5px 5px 9px #0e0f12,-5px -5px 9px #2e333c}@media(max-width: 768px){.app-body .redeem-title .submit-code{margin-left:0px;margin-top:10px}}.app-body .redeem-title .submit-code:hover{background-color:#2f3135;color:#ffae88;box-shadow:none}.coupons{padding:20px;margin-top:10px;background-color:#1e2127;box-shadow:inset 18px 18px 35px #0e0f12,inset -18px -18px 35px #2e333c;border-radius:10px;height:25vh;overflow:scroll}.coupon-container{width:50vw;max-width:300px;height:20vw;max-height:100px;margin-bottom:40px;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;border:5px solid #ffae88;background-color:#fff4ef;color:#1e2127;text-transform:uppercase;border-radius:10px;box-shadow:18px 18px 35px #0e0f12,-18px -18px 35px #2e333c}.coupon-container:last-child{margin-bottom:0px}.coupon-container .code{padding-bottom:20px;-webkit-transform:rotate(-90deg);transform:rotate(-90deg);border-bottom:1px dashed #ffae88;font-family:"Oswald",Arial,Helvetica,sans-serif;font-size:calc(8px + 2vmin);width:30vw;max-width:100px}.coupon-container .gift{width:100%}
